// src/components/SettingItem.js

import React from 'react';
import './SettingItem.css'; // You can add styles specific to SettingItem here

const SettingItem = ({ title, description, isChecked, onToggle }) => {
    return (
        <div className="setting-item">
            <div className="setting-info">
                <h2 className="setting-Title">{title}</h2>
                <p className="setting-text">{description}</p>
            </div>
            <label className="toggle-switch">
                <input
                    type="checkbox"
                    checked={isChecked}
                    onChange={onToggle} // Handle toggle change
                />
                <span className="slider"></span>
            </label>
        </div>
    );
};

export default SettingItem;
