import React, { useEffect, useState } from 'react';
import { Outlet } from 'react-router-dom'; // Outlet for rendering nested routes
import Navbar from '../components/Navbar'; // Import the Navbar
import bankingData from '../UserData.json'; // Import your JSON data
import ScrollToTop from '../components/ScrollToTop'; // Import ScrollToTop
import "./DashboardLayout.css";
import Header from '../components/header';
import Loader from '../components/Loader';

const DashboardLayout = () => {
    const [user, setUser] = useState(null); // State to store user data

    useEffect(() => {
        // Simulate a data fetch (replace this with an actual API call if needed)
        const fetchedUser = bankingData.users[0]; // For example, get the first user
        setUser(fetchedUser);
    }, []);

    if (!user) {
        return <Loader />; // Show a loading message while data is being fetched
    }

    return (
        <div className="dashboard-layout">
            <ScrollToTop />
            <header>
                <Header user={user} /> {/* Pass user directly */}
            </header>
            <main>
                <Outlet context={{ user }} />
            </main>
            <footer>
                <Navbar />
            </footer>
        </div>
    );
};

export default DashboardLayout;
