import React from 'react';
import { useOutletContext } from 'react-router-dom';
import BalanceCard from '../components/BalanceCard';
import TransactionList from '../components/TransactionList';
import Actions from '../components/Actions';
import SpendingChart from '../components/SpendingChart';
import './Home.css';

const Home = () => {
    const { user } = useOutletContext();
    const creditCards = user.creditCards || [];
    const transactions = [];

    // Collect transactions from all accounts
    user.accounts.forEach(account => {
        if (account.transactions) {
            transactions.push(...account.transactions);
        }
    });

    // Sort transactions by date, newest first
    transactions.sort((a, b) => new Date(b.date) - new Date(a.date));

    // Limit to the latest 6 transactions
    const limit = 5;

    // Action Handlers
    const handleTransfer = () => {
        console.log('Transfer Money clicked');
        // Logic to navigate to or open Transfer Money functionality
    };

    const handlePayBill = () => {
        console.log('Pay Bill clicked');
        // Logic to navigate to or open Pay Bill functionality
    };

    const handleDepositCheck = () => {
        console.log('Deposit Check clicked');
        // Logic to navigate to or open Deposit Check functionality
    };

    const handleViewStatements = () => {
        console.log('View Statements clicked');
        // Logic to navigate to or open View Statements functionality
    };

    return (
        <div className="home">
            <BalanceCard />
            <Actions 
                onTransfer={handleTransfer}
                onPayBill={handlePayBill}
                onDepositCheck={handleDepositCheck}
                onViewStatements={handleViewStatements}
            />
            <SpendingChart transactions={transactions} />
            <TransactionList transactions={transactions} limit={limit} />
        </div>
    );
};

export default Home;
