import React from 'react';
import './CreditCards.css';

const CreditCards = ({ creditCards = [] }) => { // Default to an empty array
    return (
        <div className="CreditCards">
            {creditCards.length === 0 ? (
                <p>No credit cards available.</p>
            ) : (
                creditCards.map((card) => (
                    <div key={card.id} className="CreditCard">
                        <p className="Label">Card Type</p>
                        <div className="Card">
                            <div className="Labels">
                                <p className="LabelsP1">{card.type}</p>
                                <p className="LabelsP2">kort</p>
                            </div>
                            <div className="CardNumbers">
                                <p className="Numbers">{card.number}</p>
                            </div>
                            <div className="DateAndBalance">
                                <p className="Date">{card.expiryDate}</p>
                                <p className="Balance">{card.accountNumber}</p>
                            </div>
                        </div>
                    </div>
                ))
            )}
        </div>
    );
};

export default CreditCards;
