import React from 'react';
import { useOutletContext } from 'react-router-dom'; // Import hook to access outlet context
import CreditCards from '../components/CreditCards';
import './Security.css';

const Security = () => {
    const { user } = useOutletContext();
    const creditCards = user.creditCards || [];

    return (
        <div className="Add">
            <CreditCards creditCards={creditCards} />
        </div>
    );
};

export default Security;
