// src/components/NotMobileMessage.js
import React from 'react';
import './NotMobileMessage.css'; // Import the CSS file

const NotMobileMessage = () => (
    <div className="not-mobile-message">
        <h2>This application is designed for mobile devices only.</h2>
        <p>Please access this application from a mobile device.</p>
    </div>
);

export default NotMobileMessage;
