import React, { useRef, useEffect } from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import './SpendingChart.css'; // Import the CSS file

// Register chart.js modules
ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const SpendingChart = ({ transactions = [] }) => {
    const chartRef = useRef(null);

    // Utility function to calculate monthly earnings and spending
    const calculateMonthlyData = (transactions) => {
        const monthlyData = {};

        // Get the current date and the date six months ago
        const currentDate = new Date();
        const sixMonthsAgo = new Date(currentDate.setMonth(currentDate.getMonth() - 6));

        // Filter transactions to include only the last six months
        transactions.forEach((transaction) => {
            const transactionDate = new Date(transaction.date);
            if (transactionDate >= sixMonthsAgo) {
                const month = transactionDate.toLocaleString('da-DK', { month: 'short', year: 'numeric' });
                if (!monthlyData[month]) {
                    monthlyData[month] = { income: 0, expense: 0 };
                }

                // Sum up income and expenses for each month
                if (transaction.type === 'income') {
                    monthlyData[month].income += transaction.amount; // Positive for income
                } else if (transaction.type === 'expense') { // Assuming 'expense' is used for spending
                    monthlyData[month].expense += Math.abs(transaction.amount); // Treat expenses as positive for the chart
                }
            }
        });

        return Object.keys(monthlyData)
            .map(month => ({
                month,
                income: monthlyData[month].income,
                expense: monthlyData[month].expense,
            }))
            .sort((a, b) => new Date(a.month) - new Date(b.month));
    };

    const monthlyData = calculateMonthlyData(transactions);

    const createGradient = (chart) => {
        const ctx = chart.ctx; // Get the canvas context from the chart instance
        const chartArea = chart.chartArea; // Get the chart area

        // Create gradient for expenses
        const gradient1 = ctx.createLinearGradient(chartArea.left, chartArea.top, chartArea.left, chartArea.bottom);
        gradient1.addColorStop(0, '#f67b2b'); // Start color
        gradient1.addColorStop(1, '#fb0363'); // End color

        // Create gradient for income
        const gradient2 = ctx.createLinearGradient(chartArea.left, chartArea.top, chartArea.left, chartArea.bottom);
        gradient2.addColorStop(0, '#42adf5'); // Start color
        gradient2.addColorStop(1, '#2761e3'); // End color

        return [gradient1, gradient2];
    };

    const data = {
        labels: monthlyData.map(item => item.month),
        datasets: [
            {
                label: 'Udgifter (DKK)', // Spending
                data: monthlyData.map(item => item.expense), // Positive values for spending
                backgroundColor: (context) => {
                    const chart = context.chart; // Get the chart instance
                    if (!chart.chartArea) {
                        // Return a single color if chartArea is not yet available
                        return '#FF512F';
                    }
                    const gradients = createGradient(chart); // Call the gradient function
                    return gradients[0]; // Return expense gradient
                },
                borderWidth: 0, // Remove border
            },
            {
                label: 'Indkomst (DKK)', // Income
                data: monthlyData.map(item => item.income), // Positive values for income
                backgroundColor: (context) => {
                    const chart = context.chart; // Get the chart instance
                    if (!chart.chartArea) {
                        // Return a single color if chartArea is not yet available
                        return '#4CAF50';
                    }
                    const gradients = createGradient(chart); // Call the gradient function
                    return gradients[1]; // Return income gradient
                },
                borderWidth: 0, // Remove border
            },
        ],
    };

    const options = {
        responsive: true,
        maintainAspectRatio: false,
        scales: {
            x: {
                ticks: { color: 'rgba(255, 255, 255, 1)' },
                grid: { display: false },
            },
            y: {
                beginAtZero: true, // Start at zero
                ticks: {
                    color: 'rgba(255, 255, 255, 1)',
                },
                grid: {
                    color: 'rgba(255, 255, 255, 0.2)', // Lighter grid color for better contrast
                },
            },
        },
        plugins: {
            legend: {
                display: true,
                labels: {
                    color: 'rgba(255, 255, 255, 1)',
                },
            },
            title: {
                display: true,
                text: 'Månedligt Forbrug og Indkomst',
                color: 'rgba(255, 255, 255, 1)',
                font: {
                    size: 20,
                },
                padding: { top: 0, bottom: 10 },
            },
        },
    };

    useEffect(() => {
        const chart = chartRef.current;
        if (chart) {
            chart.update();
        }
    }, [monthlyData]);

    return (
        <div className="spending-chart">
            <Bar ref={chartRef} data={data} options={options} />
        </div>
    );
};

export default SpendingChart;
