import React from 'react';
import { Link } from 'react-router-dom';
import './Navbar.css';

// Import your icons
import plusIcon from '../icons/plus.webp';
import homeIcon from '../icons/home.webp';
import shieldIcon from '../icons/shield.webp';
import settingsIcon from '../icons/settings.webp';
import profileIcon from '../icons/profile.webp';

const Navbar = () => {
    return (
        <nav className="navbar">
            <ul className="navbar-list">
                <li className="navbar-item">
                    <Link to="/dashboard/add" aria-label="Add a new item">
                        <img src={plusIcon} alt="Add" className="nav-icon" />
                    </Link>
                </li>
                <li className="navbar-item">
                    <Link to="/dashboard/security" aria-label="Security settings">
                        <img src={shieldIcon} alt="Security" className="nav-icon" />
                    </Link>
                </li>
                <li className="navbar-item">
                    <Link to="/dashboard/home" aria-label="Dashboard Home">
                        <img src={homeIcon} alt="Dashboard" className="nav-icon" />
                    </Link>
                </li>
                <li className="navbar-item">
                    <Link to="/dashboard/profile" aria-label="Profile settings">
                        <img src={profileIcon} alt="Profile" className="nav-icon" />
                    </Link>
                </li>
                <li className="navbar-item">
                    <Link to="/dashboard/settings" aria-label="Settings">
                        <img src={settingsIcon} alt="Settings" className="nav-icon" />
                    </Link>
                </li>
            </ul>
        </nav>
    );
};

export default Navbar;
