// src/components/Header.js
import React from 'react';
import { useNavigate } from 'react-router-dom'; // Import useNavigate for navigation
import './Header.css'; // Importing the CSS for styling
import logOutIcon from '../icons/logout.webp'; // Import the logout icon
import notificationIcon from '../icons/notification.webp'; // Import the logout icon
import { useAuth } from '../AuthContext'; // Import the AuthContext

const Header = ({ user }) => {
    const navigate = useNavigate(); // Initialize useNavigate
    const { logout } = useAuth(); // Get the logout function from AuthContext

    // Check if user exists and has the necessary properties
    if (!user) {
        return <div>Indlæser brugerdata...</div>; // Loading message in Danish
    }

    // Extract user information
    const { personalInfo } = user;

    // Function to get initials from the user's full name
    const getInitials = (name) => {
        const names = name.split(' '); // Split the name by spaces
        const firstInitial = names[0]?.charAt(0).toUpperCase() || ''; // First letter of the first name
        const lastInitial = names[names.length - 1]?.charAt(0).toUpperCase() || ''; // First letter of the last name
        return `${firstInitial}${lastInitial}`; // Return initials without space for a compact look
    };

    // Logout function
    const handleLogout = () => {
        logout(); // Call the logout function from AuthContext
        navigate('/'); // Navigate to the login page
    };
    const handlenotification = () => {
        console.log("notification clicked");
    }

    return (
        <div className="Header">
            <div className='Logout-con' onClick={handleLogout} style={{ cursor: 'pointer' }}>
                <img src={logOutIcon} alt='Logout Icon' className='Logout-icon' />
            </div>
            <div className='Header-con'>
                <p className="Header-img-text">
                    {getInitials(personalInfo.name)}
                </p>
            </div>
            <div className='notification-con'>
                <div className='notification-btn' onClick={handlenotification} style={{ cursor: 'pointer' }}>
                    <img src={notificationIcon} alt='notification Icon' className='notification-icon' />
                    <div className="notification-number">
                        <p className='TheNumber'>8</p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Header;
