import React from 'react';
import './TransactionList.css';

// Utility function to format the date in Danish
const formatDate = (dateString) => {
    const options = { day: 'numeric', month: 'short', year: 'numeric' };
    return new Date(dateString).toLocaleDateString('da-DK', options); // Use 'da-DK' for Danish format
};

const TransactionList = ({ transactions = [], limit }) => {
    // Slice the transactions array based on the limit
    const displayedTransactions = limit ? transactions.slice(0, limit) : transactions;

    return (
        <div className="transaction-list">
            <div className="transaction-title">
                <h2 className="title">Seneste Transaktioner</h2>
            </div>
            <ul>
                {displayedTransactions.length === 0 ? (
                    <li>Ingen transaktioner tilgængelige.</li>
                ) : (
                    displayedTransactions.map((transaction, index) => (
                        <li key={index}> {/* Use index as key since there is no unique ID */}
                            <div className="data">
                                <p className="description">{transaction.description}</p>
                                <p
                                    className={`amount ${transaction.type === 'income' ? 'income' : 'expense'}`}
                                >
                                    {transaction.type === 'income' ? '+' : '-'}
                                    {Math.abs(transaction.amount).toLocaleString('da-DK', {
                                        minimumFractionDigits: 2,
                                        maximumFractionDigits: 2,
                                    })} DKK
                                </p>
                            </div>
                            <div className="info">
                                <p className="text">{transaction.type}</p>
                                <p className="text">{formatDate(transaction.date)}</p>
                            </div>
                        </li>
                    ))
                )}
            </ul>
        </div>
    );
};

export default TransactionList;
