// src/AuthContext.js
import React, { createContext, useContext, useState, useEffect } from 'react';
import Cookies from 'js-cookie';

const AuthContext = createContext();

const MAX_ATTEMPTS = 5;
const LOCKOUT_TIME = 30000; // 30 seconds

export const AuthProvider = ({ children }) => {
    const [isAuthenticated, setIsAuthenticated] = useState(() => {
        return Cookies.get('isAuthenticated') === 'true';
    });
    const [attempts, setAttempts] = useState(() => {
        return Number(Cookies.get('attempts')) || 0;
    });
    const [lockedOut, setLockedOut] = useState(() => {
        return Cookies.get('lockedOut') === 'true';
    });

    useEffect(() => {
        if (lockedOut) {
            const timer = setTimeout(() => {
                setLockedOut(false);
                setAttempts(0);
                Cookies.set('lockedOut', 'false', { expires: 1 });
                Cookies.set('attempts', 0, { expires: 1 });
            }, LOCKOUT_TIME);

            return () => clearTimeout(timer);
        }
    }, [lockedOut]);

    const login = () => {
        setIsAuthenticated(true);
        setAttempts(0);
        setLockedOut(false);
        Cookies.set('isAuthenticated', 'true', { expires: 1 }); // Expires in 1 day
        Cookies.set('attempts', 0, { expires: 1 });
        Cookies.set('lockedOut', 'false', { expires: 1 });
    };

    const logout = () => {
        setIsAuthenticated(false);
        setAttempts(0);
        setLockedOut(false);
        Cookies.remove('isAuthenticated');
        Cookies.remove('attempts');
        Cookies.remove('lockedOut');
    };

    const handleFailedLogin = () => {
        setAttempts((prevAttempts) => {
            const newAttempts = prevAttempts + 1;
            Cookies.set('attempts', newAttempts, { expires: 1 });
            if (newAttempts >= MAX_ATTEMPTS) {
                setLockedOut(true);
                Cookies.set('lockedOut', 'true', { expires: 1 });
            }
            return newAttempts;
        });
    };

    return (
        <AuthContext.Provider value={{ isAuthenticated, login, logout, handleFailedLogin, lockedOut, }}>
            {children}
        </AuthContext.Provider>
    );
};

export const useAuth = () => {
    return useContext(AuthContext);
};
