import React from 'react';
import { useOutletContext } from 'react-router-dom'; // Import useOutletContext
import './Profile.css'; // Importing the CSS for styling

const Profile = () => {
    const { user } = useOutletContext(); // Get user from the outlet context

    // Check if user exists and has the necessary properties
    if (!user) {
        return <div>Indlæser brugerdata...</div>; // Loading message in Danish
    }

    // Extract user information
    const { personalInfo, settings, accounts } = user;

    // Function to get initials from the user's full name
    const getInitials = (name) => {
        const names = name.split(' '); // Split the name by spaces
        const firstInitial = names[0]?.charAt(0).toUpperCase() || ''; // First letter of the first name
        const lastInitial = names[names.length - 1]?.charAt(0).toUpperCase() || ''; // First letter of the last name
        return `${firstInitial} ${lastInitial}`; // Return initials with a space in between
    };

    return (
        <div className="profile">
            {/* User Information */}
            <section className="user-info">
                <div className="profile-img-container">
                    <div className="profile-img">
                        <div className="profile-img-text">
                            {getInitials(personalInfo.name)}
                        </div>
                    </div>
                </div>
                <div>
                    <p className="profile-text">{personalInfo.name}</p>
                </div>
                <div>
                    <p className="profile-text">{personalInfo.email}</p>
                </div>
                <div className="profile-split">
                    <div className="profile-text-container">
                        <p className="profile-title"><strong>Sprog:</strong></p>
                        <p className="profile-text">{settings.language}</p>
                    </div>
                    <div className="profile-text-container">
                        <p className="profile-title"><strong>Valuta:</strong></p>
                        <p className="profile-text">{settings.currency}</p>
                    </div>
                </div>
            </section>

            {/* Account Details */}
            <section className="account-info">
                <h2 className="account-info-title">Kontoinformation</h2>
                {accounts && accounts.length > 0 ? (
                    accounts.map((account, index) => (
                        <div key={index} className="account-details">
                            <p className="account-info-text"><strong>Kontotype:</strong> {account.accountType}</p>
                            <p className="account-info-text"><strong>Kontonummer:</strong> {account.accountNumber}</p>
                            <p className="account-info-text"><strong>Saldo:</strong> {account.balance} DKK</p>
                        </div>
                    ))
                ) : (
                    <p>Ingen konti fundet.</p>
                )}
            </section>
        </div>
    );
};

export default Profile;
