import React, { useEffect, useState } from 'react';
import bankingData from '../UserData.json'; // Adjust the path based on your folder structure
import './BalanceCard.css';

const BalanceCard = () => {
    const [accounts, setAccounts] = useState([]); // State to hold account data

    useEffect(() => {
        // Fetch the user data from JSON (or API)
        const user = bankingData.users[0]; // Assuming we are interested in the first user
        setAccounts(user.accounts); // Set the accounts data
    }, []);

    return (
        <div className="balance-card">
            <div className="Title-Container">
                <h2 className="balanceTitle">Din balance</h2>
            </div>
            <div className="balance-Container">
                <div className="balance-item">
                    {accounts.map((account, index) => (
                        <div className="balance-bar" key={index}>
                            <div className="balance-texts">
                                <h3 className="balance-title">{account.accountType}</h3>
                                <p className="balance-text">{account.accountNumber}</p>
                            </div>
                            <div className="balance-number">
                                <p className="balance-numbers">{account.balance.toLocaleString('da-DK', { style: 'currency', currency: 'DKK' })}</p>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </div>
    );
};

export default BalanceCard;
