// src/pages/Settings.js

import React, { useEffect, useState } from 'react';
import { useOutletContext } from 'react-router-dom'; // Import useOutletContext
import SettingItem from '../components/SettingItem'; // Adjust the path as necessary
import './Settings.css'; // Import the CSS for styling

const Settings = () => {
    const { user } = useOutletContext(); // Get user from the outlet context
    const [settingsData, setSettingsData] = useState([]);
    const [preferences, setPreferences] = useState({}); // Local state to manage toggle states

    // Fetch settings data from the user passed via context
    useEffect(() => {
        if (user) {
            const userNotificationSettings = user.settings.notificationPreferences; // Notification settings
            const userAppSettings = user.settings.appSettings; // App settings

            const notificationSettings = Object.keys(userNotificationSettings).map(key => ({
                title: key.charAt(0).toUpperCase() + key.slice(1).replace(/([A-Z])/g, ' '), // Capitalize the title
                description: `Manage ${key.replace(/([A-Z])/g, ' ').toLowerCase()} notifications.`,
                preferenceKey: key,
                isChecked: userNotificationSettings[key], // Get the initial checked status
            }));

            const appSettings = Object.keys(userAppSettings).map(key => ({
                title: key.charAt(0).toUpperCase() + key.slice(1).replace(/([A-Z])/g, ' '), // Capitalize the title
                description: `Manage ${key.replace(/([A-Z])/g, ' ').toLowerCase()} settings.`,
                preferenceKey: key,
                isChecked: userAppSettings[key], // Get the initial checked status
            }));

            setSettingsData([...notificationSettings, ...appSettings]); // Combine both settings
            setPreferences({ ...userNotificationSettings, ...userAppSettings }); // Initialize preferences state
        }
    }, [user]); // Dependency on user data

    // Function to handle toggle changes
    const handleToggle = (preferenceKey) => {
        setPreferences((prev) => ({
            ...prev,
            [preferenceKey]: !prev[preferenceKey] // Toggle the selected preference
        }));
    };

    return (
        <div className="settings">
            <div className="settings-Container">
                <div className="settings-TitleCon">
                    <h1 className="settings-Title">Indstillinger</h1> {/* Settings in Danish */}
                </div>
                <div className="settings-Box">
                    {settingsData.map((setting) => (
                        <SettingItem
                            key={setting.preferenceKey}
                            title={setting.title}
                            description={setting.description}
                            isChecked={preferences[setting.preferenceKey]} // Use the local preferences state for the toggle
                            onToggle={() => handleToggle(setting.preferenceKey)} // Call the toggle handler
                        />
                    ))}
                </div>
            </div>
        </div>
    );
};

export default Settings;
