import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../AuthContext';
import './Login.css';
import clearIcon from '../icons/clear.webp';
import questionMarkIcon from '../icons/question-mark.webp';
import faceIDIcon from '../icons/face-id.webp';
import logoIcon from '../icons/logo.webp';
import goBackIcon from '../icons/goback.webp';
import bankingData from '../UserData.json'; // Import your JSON data

// Extract users array from bankingData
const Login = ({ users = bankingData.users }) => {  // Default fallback to bankingData.users
    const [pin, setPin] = useState('');
    const [animationClass, setAnimationClass] = useState('');
    const [errorMessage, setErrorMessage] = useState('');
    const [loginSuccess, setLoginSuccess] = useState(false);
    const navigate = useNavigate();
    const { login, handleFailedLogin, lockedOut } = useAuth();

    const handleKeyClick = (value) => {
        if (lockedOut) return;
        
        if (value === 'help') {
            navigate('/questions');
            return;
        }

        if (pin.length < 6 && value !== '') {
            const newPin = pin + value;
            setPin(newPin);
            if (newPin.length === 6) {
                validatePin(newPin);
            }
        }
    };

    const validatePin = (inputPin) => {
        // Safely check if users exist before finding a user
        if (!users || users.length === 0) {
            console.error("User data is missing or empty");
            setErrorMessage('User data is unavailable.');
            return;
        }

        // Find the user with a matching password
        const user = users.find(user => user.personalInfo.password === inputPin);

        if (user) {  // If a user is found with the matching password
            console.log('Logged in with PIN:', inputPin);
            setLoginSuccess(true);
            setTimeout(() => {
                login();
                navigate('/dashboard');
            }, 1000);
        } else {
            console.log('Invalid PIN');
            setErrorMessage('Invalid PIN. Please try again.');
            handleInvalidPin();
            handleFailedLogin();
        }
    };

    const handleInvalidPin = () => {
        setAnimationClass('shake');
        const shakeDuration = 500;

        setTimeout(() => {
            const intervalId = setInterval(() => {
                setPin((prevPin) => {
                    if (prevPin.length === 0) {
                        clearInterval(intervalId);
                        setAnimationClass('');
                        return prevPin;
                    }
                    return prevPin.slice(0, -1);
                });
            }, 100);
        }, shakeDuration);
    };

    const handleClear = () => {
        setPin((prevPin) => prevPin.slice(0, -1));
    };

    const handleGoBack = () => {
        navigate('/');
    };

    return (
        <div className={`login-container ${loginSuccess ? 'login-success-animation' : ''}`}>
            <img src={goBackIcon} alt="Go Back" className="goBack-icon" onClick={handleGoBack} />
            <img src={faceIDIcon} alt="Login" className="faceID-icon" />
            <img src={logoIcon} alt="logo" className="logo-icon" />
            <div className={`pin-display ${animationClass}`}>
                {Array.from({ length: 6 }).map((_, index) => (
                    <div key={index} className={`number ${pin[index] ? 'filled' : 'empty'}`}></div>
                ))}
            </div>
            {errorMessage && <div className="error-message">{errorMessage}</div>}
            {lockedOut && <div className="error-message">Account is locked. Please try again later.</div>}
            <form className="keypad">
                <div className="keypad-row">
                    <button className="loginButton" type="button" onClick={() => handleKeyClick('1')} disabled={lockedOut}>1</button>
                    <button className="loginButton" type="button" onClick={() => handleKeyClick('2')} disabled={lockedOut}>2</button>
                    <button className="loginButton" type="button" onClick={() => handleKeyClick('3')} disabled={lockedOut}>3</button>
                </div>
                <div className="keypad-row">
                    <button className="loginButton" type="button" onClick={() => handleKeyClick('4')} disabled={lockedOut}>4</button>
                    <button className="loginButton" type="button" onClick={() => handleKeyClick('5')} disabled={lockedOut}>5</button>
                    <button className="loginButton" type="button" onClick={() => handleKeyClick('6')} disabled={lockedOut}>6</button>
                </div>
                <div className="keypad-row">
                    <button className="loginButton" type="button" onClick={() => handleKeyClick('7')} disabled={lockedOut}>7</button>
                    <button className="loginButton" type="button" onClick={() => handleKeyClick('8')} disabled={lockedOut}>8</button>
                    <button className="loginButton" type="button" onClick={() => handleKeyClick('9')} disabled={lockedOut}>9</button>
                </div>
                <div className="keypad-row">
                    <button type="button" onClick={() => handleKeyClick('help')} className="icon-button loginButton" aria-label="Help" disabled={lockedOut}>
                        <img src={questionMarkIcon} alt="?" className="icon" />
                    </button>
                    <button className="loginButton" type="button" onClick={() => handleKeyClick('0')} disabled={lockedOut}>0</button>
                    <button type="button" onClick={handleClear} className="icon-button loginButton" aria-label="Clear" disabled={lockedOut}>
                        <img src={clearIcon} alt="Clear" className="icon" />
                    </button>
                </div>
            </form>
        </div>
    );
};

export default Login;
